.Landing {
  padding-top: 30px;
  width: 100%;
  background-color: #d6ccc2;
  height: 100%;
}
.flexbox {
  display: flex;
  flex-direction: row;
  height: 400px;
  align-items: center; /* align items vertically middle */
  justify-content: center; /* center items horizontally in middle columns */
  height: 100%;
  padding: 20px;
}

.col1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-size: 3rem;
  /* flex: 0 0 50%; */
  color: #FFFFFF;
  padding: 30px;
  padding-top: 50px;
  padding-left: 20%;
  width: 100%;
  height: 400px;
}

.experience {
  padding-top: 20px;
}

.experience-section {
  max-width: 80%;
  margin: 2px auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.header-section {
  padding: 10px;
}

.experience-row {
  margin-bottom: 20px;
  border:2px solid #f4f4f4;
}

.col2 {
  flex: 1;
  padding: 20px;
}


